}
.bgimg-2 {
  background-image: url("./Assets/Images/neoncar.jpeg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  max-height: 100%;
}
.bgimg-3 {
