#top {
  overflow: hidden;
}

#top h2 {
  font-family: "Roboto", sans-serif;
  color: lightgray;
  font-size: 1.6rem;
  position: relative;
  margin-right: 0rem;
  margin-left: 0%;
  padding: 0.8rem 0;
}

#top h2::before {
  background-color: black;
  bottom: 0;
  content: " ";
  left: 0rem;
  position: absolute;
  right: 0;
  transform-origin: 100% 0;
  transform: skew(-40deg);
  top: 0;
  z-index: -1;
}

#top-2 {
  overflow: hidden;
}

#top-2 h2 {
  font-family: "Roboto", sans-serif;
  color: lightgray;
  font-size: 1.6rem;
  position: relative;
  margin-left: 6rem;
  padding: 0.8rem 0;
}

#top-2 h2::before {
  background-color: black;
  bottom: 0;
  content: " ";
  left: -9999rem;
  position: absolute;
  right: 0;
  transform-origin: 100% 0;
  transform: skew(-40deg);
  top: 0;
  z-index: -1;
}

